import { Injectable } from '@angular/core';
import {
  Router,
  Resolve,
  RouterStateSnapshot,
  ActivatedRouteSnapshot,
} from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import {
  FunctionDetailsFragment,
  GetFunctionDetailsGQL,
  LanguageCode,
} from '../../../graphql/graphql';

@Injectable()
export class FunctionResolver
  implements Resolve<FunctionDetailsFragment | undefined | null> {
  constructor(
    private getFunctionDetailsGQL: GetFunctionDetailsGQL,
    private router: Router,
  ) {}

  resolve(
    route: ActivatedRouteSnapshot,
  ):
    | FunctionDetailsFragment
    | Observable<FunctionDetailsFragment | undefined>
    | Promise<FunctionDetailsFragment | undefined>
    | null
    | undefined {
    const id = parseInt(route.paramMap.get('id') || '', 10);
    if (id) {
      return this.getFunctionDetailsGQL
        .fetch(
          {
            where: {
              id,
            },
          },
          {
            fetchPolicy: 'network-only',
          },
        )
        .pipe(
          map((result) => {
            if (result.error || result.errors) {
              this.router.navigateByUrl('/exception/500');
              return undefined;
            }

            if (result.data?.function) {
              return result.data.function;
            } else {
              this.router.navigateByUrl('/exception/404');
              return undefined;
            }
          }),
        );
    } else {
      return {
        translations: [
          {
            language: LanguageCode.Vi,
          },
        ],
      } as FunctionDetailsFragment;
    }
  }
}
