import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { QueryRef } from 'apollo-angular';
import { NzMessageService } from 'ng-zorro-antd/message';
import { NzTableQueryParams } from 'ng-zorro-antd/table';
import { Subject, Subscription } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import {
  GetFunctionListGQL,
  GetFunctionListQuery,
  GetFunctionListQueryVariables,
  OrderBy,
} from '../../../../graphql/graphql';

@Component({
  selector: 'app-function-list',
  templateUrl: './function-list.component.html',
  styleUrls: ['./function-list.component.scss'],
})
export class FunctionListComponent implements OnInit, OnDestroy {
  destroy$: Subject<boolean> = new Subject();
  functions: GetFunctionListQuery['functions'] = [];
  functionsCount = 0;
  pageIndex = 1;
  pageSize = 10;
  loading = false;
  dataQueryRef:
    | QueryRef<GetFunctionListQuery, GetFunctionListQueryVariables>
    | undefined;
  dataSubscription: Subscription | undefined;

  searchForm: FormGroup;
  text?: string;

  constructor(
    private fb: FormBuilder,
    private router: Router,
    private route: ActivatedRoute,
    private getFunctionListGQL: GetFunctionListGQL,
    private msgService: NzMessageService,
    private translateService: TranslateService,
  ) {
    this.searchForm = this.fb.group({
      name: [null],
    });
  }

  ngOnInit(): void {
    this.route.queryParamMap
      .pipe(takeUntil(this.destroy$))
      .subscribe((paramMap) => {
        this.pageIndex = parseInt(paramMap.get('page') || '1', 10);
        this.pageSize = parseInt(paramMap.get('size') || '10', 10);
        this.text = paramMap.get('text') || '';

        this.patchFormValue();

        if (this.dataSubscription) {
          this.dataQueryRef?.refetch(this.getVariables());
        } else {
          this.dataQueryRef = this.getFunctionListGQL.watch(
            this.getVariables(),
            {
              notifyOnNetworkStatusChange: true,
              fetchPolicy: 'network-only',
            },
          );
          this.dataSubscription = this.dataQueryRef.valueChanges
            .pipe(takeUntil(this.destroy$))
            .subscribe(({ data, loading, error, errors }) => {
              this.loading = loading;
              if (loading) {
                return;
              }

              if (error || errors) {
                this.msgService.error(
                  this.translateService.instant('ui.message.can_not_load_data'),
                );
                return;
              }

              if (data) {
                this.functions = data.functions;
                this.functionsCount = data.functionsConnection.aggregate.count;
              }
            });
        }
      });
  }

  private getVariables(): GetFunctionListQueryVariables {
    return {
      first: this.pageSize,
      skip: (this.pageIndex - 1) * this.pageSize,
      where: {
        translationsSome: {
          nameContains: this.text?.trim() || undefined,
        },
      },
      orderBy: {
        id: OrderBy.Desc,
      },
    };
  }

  private patchFormValue(): void {
    if (this.text && this.searchForm.controls.name.untouched) {
      this.searchForm.patchValue({
        name: this.text,
      });
    }
  }

  handleQueryParamsChange(params: NzTableQueryParams): void {
    this.router.navigate([], {
      queryParams: {
        page: params.pageIndex,
        size: params.pageSize,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  handleSearch(): void {
    const searchData = this.searchForm.value;
    const text = searchData.name || undefined;

    this.router.navigate([], {
      queryParams: {
        text,
        page: 1,
      },
      relativeTo: this.route,
      queryParamsHandling: 'merge',
    });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
