import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { FunctionDetailsComponent } from './pages/function-details/function-details.component';
import { FunctionListComponent } from './pages/function-list/function-list.component';
import { FunctionResolver } from './resolvers/function.resolver';

const routes: Routes = [
  {
    path: 'new',
    resolve: {
      function: FunctionResolver,
    },
    component: FunctionDetailsComponent,
  },
  {
    path: ':id',
    resolve: {
      function: FunctionResolver,
    },
    component: FunctionDetailsComponent,
  },
  {
    path: '',
    pathMatch: 'full',
    component: FunctionListComponent,
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class FunctionRoutingModule {}
