import { Location } from '@angular/common';
import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Data } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { NzMessageService } from 'ng-zorro-antd/message';
import { NzUploadFile } from 'ng-zorro-antd/upload';
import { Subject, Observable, of } from 'rxjs';
import { map, mergeMap, takeUntil } from 'rxjs/operators';
import _ from 'lodash';

import { environment } from '../../../../environments/environment';
import {
  CreateFunctionGQL,
  FunctionCreateInput,
  FunctionDetailsFragment,
  FunctionUpdateInput,
  LanguageCode,
  SpecialIngredientFunctionType,
  UpdateFunctionGQL,
} from '../../../../graphql/graphql';
import { FileService } from '../../../shared/services/file/file.service';
import { LanguageService } from '../../../shared/services/language/language.service';

@Component({
  selector: 'app-function-details',
  templateUrl: './function-details.component.html',
  styleUrls: ['./function-details.component.scss'],
})
export class FunctionDetailsComponent implements OnInit {
  LanguageCode = LanguageCode;
  FunctionType = SpecialIngredientFunctionType;
  destroy$: Subject<boolean> = new Subject();
  validateForm: FormGroup;
  function?: FunctionDetailsFragment;
  loading = false;
  symbolFile?: NzUploadFile;

  constructor(
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private location: Location,
    private createFunctionGQL: CreateFunctionGQL,
    private updateFunctionGQL: UpdateFunctionGQL,
    private msgService: NzMessageService,
    private translateService: TranslateService,
    private languageService: LanguageService,
    private fileService: FileService,
    private httpClient: HttpClient,
  ) {
    this.validateForm = this.fb.group({
      type: [SpecialIngredientFunctionType.Advantage, Validators.required],
      symbolUrl: [null, Validators.required],
      translations: this.fb.array([], Validators.required),
    });
  }

  ngOnInit(): void {
    this.route.data
      .pipe(
        takeUntil(this.destroy$),
        map<Data, FunctionDetailsFragment>((value) => value.function),
      )
      .subscribe((func) => {
        this.function = func;
        this.validateForm.patchValue({
          type: func.type,
          symbolUrl: func.symbolUrl,
        });
        this.validateForm.setControl(
          'translations',
          this.fb.array(
            this.languageService
              .sort([...(this.function?.translations || [])])
              .map((translation) => {
                return this.fb.group({
                  id: [translation.id],
                  name: [translation.name, Validators.required],
                  language: [translation.language, Validators.required],
                  description: [translation.description],
                });
              }) || [],
            Validators.required,
          ),
        );
      });
  }

  beforeUploadThumbnail = (file: NzUploadFile): boolean => {
    this.symbolFile = file;
    this.fileService.getBase64((file as unknown) as File, (img: string) => {
      this.validateForm.patchValue({
        symbolUrl: img,
      });
    });
    return false;
  };

  get translations(): FormArray {
    return this.validateForm.get('translations') as FormArray;
  }

  addTab(): void {
    const languages = new Set([
      LanguageCode.Vi,
      LanguageCode.En,
      LanguageCode.Ko,
    ]);
    this.translations.controls.forEach((control) =>
      languages.delete(control.value.language),
    );
    const language =
      languages.size === 0 ? LanguageCode.Vi : languages.values().next().value;

    this.translations.push(
      this.fb.group({
        id: [],
        name: [null, Validators.required],
        language: [language, Validators.required],
        description: [],
      }),
    );
  }

  removeTab(index: number): void {
    this.translations.removeAt(index);
  }

  submit(): void {
    Object.values(this.validateForm.controls).forEach((control) => {
      control.markAsDirty();
      control.updateValueAndValidity();
    });
    if (this.validateForm.invalid) {
      return;
    }

    this.loading = true;

    let symbolObservable: Observable<{
      url: string;
    }>;
    if (this.symbolFile) {
      const formData = new FormData();
      formData.append('file', (this.symbolFile as unknown) as File);
      symbolObservable = this.httpClient.post<{ url: string }>(
        `${environment.apiUri}/single-upload?useWatermark=false`,
        formData,
      );
    } else {
      symbolObservable = of({
        url: this.function?.symbolUrl || '',
      });
    }

    symbolObservable
      .pipe(
        takeUntil(this.destroy$),
        mergeMap(({ url }) => {
          if (this.function?.id) {
            const data: FunctionUpdateInput = {
              type: this.validateForm.get('type')?.value,
              symbolUrl: url,
              translations: {
                create: this.translations.value
                  ?.filter((translation: any) => !translation.id)
                  .map((translation: any) => _.omit(translation, ['id'])),
                set: this.translations.value?.filter(
                  (translation: any) => translation.id,
                ),
              },
            };
            return this.updateFunctionGQL.mutate({
              where: {
                id: this.function.id,
              },
              data,
            });
          } else {
            const data: FunctionCreateInput = {
              type: this.validateForm.get('type')?.value,
              symbolUrl: url,
              translations: {
                create: this.translations.value?.map((translation: any) =>
                  _.omit(translation, ['id']),
                ),
              },
            };
            return this.createFunctionGQL.mutate({
              data,
            });
          }
        }),
      )
      .subscribe(
        ({ errors, data }) => {
          if (errors) {
            this.msgService.error(
              this.translateService.instant(
                `function.${this.function?.id ? 'update' : 'create'}_error`,
              ),
            );
          }

          if (data) {
            this.msgService.success(
              this.translateService.instant(
                `function.${this.function?.id ? 'update' : 'create'}_success`,
              ),
            );
            this.location.back();
          }
          this.loading = false;
        },
        () => {
          this.msgService.error(
            this.translateService.instant(
              `function.${this.function?.id ? 'update' : 'create'}_error`,
            ),
          );
          this.loading = false;
        },
      );
  }

  cancel(): void {
    this.location.back();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.unsubscribe();
  }
}
